.filtros{
    display: flex;
    justify-content: center;
    width: 100%;
    button{
        background-color: transparent;
        border: none;
        cursor: pointer;
        &:hover{
            scale: 1.1;
        }
    }
    p{
        font-family: $fuente_Lato;
        margin: 0;
        font-size: 1.6rem;
    }
}
.formulario-filtros{
    height: 11.7rem;
}
.negrita{
    font-weight: bold ;
    scale: 1.1;
}