body{
    width: 100%;
    height: 100%;
    background-color: $blanco;
    position: relative;
    h1{
        font-family: $fuente_oswald;
        font-size: 8rem;
    }
}
.body-portafolio{
    background-color: $verde;
}
.contenedor-imagen{
    width: 100%;
    display: flex;
    justify-content: center;
    margin: auto  auto;
    img{
        width: 90%;
    }
}
.contenedor-degradado{
    position: absolute;
    background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.3)
  );
  width: 100%;
  height: 100%;
}
.presentacion {
    display: flex;
    flex-direction: column;
    h1{
        font-weight: bold;
    }
    h4{
        margin-top: -3rem;
        font-family: $fuente_Lato;
    }
    p{
        margin-top: 2rem;
        font-size: 2.5rem;
        font-family: $fuente_Lato;
        max-width: 85rem;
        line-height: 3.5rem;
    }
    .espacio-presentacion{
        height: 15rem;
    }
    @include tablet {
        h1{
            font-size:6rem ;
        }
        h4{
            margin-top: -3rem;
            font-family: $fuente_Lato;
        }
        p{
            font-size: 2rem;
        }
        .espacio-presentacion{
            height: 5rem;
        }
    }
}
.titulo {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    height: 15rem;
    width: 100%;
    h2 {
        font-family: $fuente_Lato;
        font-size: 4rem;
    }
    .linea {
        border-top: .2rem solid black;
        height: 1rem;
        width: 100%;
    }
}
