.seccion-contenido {
    margin: auto;
    background-color: darken($verde, $amount: 5);
    display: flex;
    flex-direction: column;
    width: 60%;
    div {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        padding-left: 2rem;
        padding-right: 2rem;
        button {
            margin: 0;
            height: 4rem;
            background-color: green;
            cursor: pointer;
        }
    }
}
.creacion{
    width: 100%;
    button{
        background-color: green;
    }
}
.lista {
display: flex;
flex-direction: column;
border: .3rem solid black;
width: 80%;
margin: auto;
gap: .3rem;
}
.segmento{
    background-color: darken($verde, $amount: 5);
    display: flex;
    width: 100%;
    justify-content: space-around;
    img{
        width: 8rem;
        height: auto;
    }
}
.eliminar{
    background-color: red;
    color: white;
    padding: .1rem .3rem;
}
.btn_actualizar {
    background-color: green;
    padding: .1rem .3rem;
    text-decoration: none;
    color: white;
    cursor: pointer;
}
