.conetenedor-galerias {
    display: flex;
    gap: 1rem;
    justify-content: center;
}
.contenedor-imagenI{
    cursor: pointer;
    display: flex;
    flex-direction: column;
    max-width: 61rem;
    gap: 1rem;
    position:relative;
    justify-content: center;
    align-items: center;
    img{
        padding: .3rem;
        width: 100%;
        height: auto;
        background-color: white;
    }
    &:hover{
        .hover-info{
            width: 100%;
            height: 100%;
            border-radius: 0%;
            background: rgb(0,0,0);
            background: linear-gradient(0deg, rgba(0,0,0,0.5046393557422969) 0%, rgba(0,0,0,0.5) 100%);
        }
    }
}
.galeria-columna{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.hover-info{
    position: absolute;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.2) 100%);
    width: 0%;
    height: 0%;
    border-radius: 50%;
    transition: all 0.5s ease-in-out;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    p{
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        font-size: 2.5rem;
    }
    span{
        margin-left: 1rem;
        font-weight: 400;
        font-size: 2rem;
    }
}
.imagenGrande{
    position: fixed;
    left: 0;
    top: -1rem;
    margin: auto;
    height: 200%;
    width: 100%;
    z-index: 999;
    display: none;
    justify-content: center;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,0.5046393557422969) 0%, rgba(0,0,0,0.8) 100%);
    .imagenGrende-contenedor{
        img{
            width: auto;
            max-width: 100vw;
            max-height: 100vh;
        }
    }
}
.bodyLock {
    overflow:unset;
  }