.header {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 10rem;
    .izquierda,.derecha{
        display: flex;
        width: 45%;
        margin-top: .7rem;
    }
    .centro{
        margin-top: 1rem;
        display: flex;
        width: 10%;
    }
    p{
        margin: 0;
    }
    @include desktopXl {
        .izquierda,.derecha {
            width: 43.5%;
        }
        .centro {
            width: 13%;
        }
    }
    @include desktop {
        .izquierda,.derecha {
            width: 40.5%;
        }
        .centro {
            width: 19%;
        }
    }
}
.portafolio{
    display: flex;
    flex-direction: column;
    height: 90%;
    padding: 1rem;
    justify-content: center;
    font-size: 2rem;
    font-family: $fuente_Lato;
    img{
        width: 3.2rem;
        height: 3.2rem;
    }
}

.icono{
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    padding-bottom: 0;
    h3{
        font-size: 2rem;
        font-family: $fuente_Lato;
        font-weight: bold;
    }
    @include tablet {
        h3{
            font-size: 1.5rem;
        }
    }
    @media (max-width: 608px) {
        h3{
            font-size: 1.2rem;
        }
    }
    img{
        width: 5.4rem;
        height: auto;
    }
    picture{
        display: flex;
    }
    .fondo-icono{
        width: 7rem;
        height: 5.71rem;
        background-color: $blanco;
        padding: .5rem;
        border-radius: 1rem;
    }
}
.icono-portafolio {
        scale: .8;
}
.redes{
    width: 21.82rem;
    display: flex;
    gap: 2rem;
    img{
        width: 3.96rem;
        height: 3.96rem;
        filter: invert(100%);
    }
    div{
        display: flex;
        justify-content: left;
        
        &:hover{
            p{
                width: 100%;
            }
            img{
                scale: 0.9;
            }
        }
        p{
            font-size: 1.5rem;
            width: 0;
            overflow: hidden;
            transition: all ease-in-out .3s;
            text-align: left;
            font-family: $fuente_Lato;
        }
    }
}
@include telefono {
    h3{
        font-size: 1rem;
    }
    .redes{
        img{
            width: 2.96rem;
            height: 2.96rem;
        }
    }
    .portafolio{
        p{
            font-size: 1.7rem;
        }
        
    }
}
.header-portafolio{
    margin-top: 1rem;
    height: 5rem;
    .centro{
        margin-top: .7rem;
        align-items: center;
        display: flex;
    }
    .redes{
        width: 21.82rem;
        display: flex;
        gap: 2rem;
        img{
            width: 3.96rem;
            height: 3.96rem;
            filter: invert(0);
        } 
    }
    p{
        color: black;
    }

}
.btn-admin{
    display: flex;
    gap: 1rem;
    width: 50%;
}