.overlayMenu{
    width: 15rem;
    height: 0;
    background-color: $blanco;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    a{
        display: flex;
        height: 33.33%;
        &:hover {
            background-color: darken($blanco, $amount: 5);
        }
        p{
            font-size: 2rem;
            font-family: $fuente_Lato;
        }
    }
}
.espacio-overlay{
    display: flex;
    width: 45%;
    height: 15rem;
    align-items: flex-start;
    position: absolute;
}