/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.centrar {
  justify-content: center;
  text-align: center;
  align-items: center;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.blanco {
  color: #FFFFFF;
}

.no_margin {
  margin: 0%;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

body {
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  position: relative;
}
body h1 {
  font-family: "Oswald", sans-serif;
  font-size: 8rem;
}

.body-portafolio {
  background-color: #d0dede;
}

.contenedor-imagen {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: auto auto;
}
.contenedor-imagen img {
  width: 90%;
}

.contenedor-degradado {
  position: absolute;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3));
  width: 100%;
  height: 100%;
}

.presentacion {
  display: flex;
  flex-direction: column;
}
.presentacion h1 {
  font-weight: bold;
}
.presentacion h4 {
  margin-top: -3rem;
  font-family: "Lato", sans-serif;
}
.presentacion p {
  margin-top: 2rem;
  font-size: 2.5rem;
  font-family: "Lato", sans-serif;
  max-width: 85rem;
  line-height: 3.5rem;
}
.presentacion .espacio-presentacion {
  height: 15rem;
}
@media (max-width: 776px) {
  .presentacion h1 {
    font-size: 6rem;
  }
  .presentacion h4 {
    margin-top: -3rem;
    font-family: "Lato", sans-serif;
  }
  .presentacion p {
    font-size: 2rem;
  }
  .presentacion .espacio-presentacion {
    height: 5rem;
  }
}

.titulo {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  height: 15rem;
  width: 100%;
}
.titulo h2 {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
}
.titulo .linea {
  border-top: 0.2rem solid black;
  height: 1rem;
  width: 100%;
}

