.btn-volver {
  margin: auto;
  background-color: red;
  text-decoration: none;
  color: white;
  padding: 0.1rem 0.3rem;
}

.header {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 10rem;
}
.header .izquierda, .header .derecha {
  display: flex;
  width: 45%;
  margin-top: 0.7rem;
}
.header .centro {
  margin-top: 1rem;
  display: flex;
  width: 10%;
}
.header p {
  margin: 0;
}
@media (max-width: 1475px) {
  .header .izquierda, .header .derecha {
    width: 43.5%;
  }
  .header .centro {
    width: 13%;
  }
}
@media (max-width: 1024px) {
  .header .izquierda, .header .derecha {
    width: 40.5%;
  }
  .header .centro {
    width: 19%;
  }
}

.portafolio {
  display: flex;
  flex-direction: column;
  height: 90%;
  padding: 1rem;
  justify-content: center;
  font-size: 2rem;
  font-family: "Lato", sans-serif;
}
.portafolio img {
  width: 3.2rem;
  height: 3.2rem;
}

.icono {
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  padding-bottom: 0;
}
.icono h3 {
  font-size: 2rem;
  font-family: "Lato", sans-serif;
  font-weight: bold;
}
@media (max-width: 776px) {
  .icono h3 {
    font-size: 1.5rem;
  }
}
@media (max-width: 608px) {
  .icono h3 {
    font-size: 1.2rem;
  }
}
.icono img {
  width: 5.4rem;
  height: auto;
}
.icono picture {
  display: flex;
}
.icono .fondo-icono {
  width: 7rem;
  height: 5.71rem;
  background-color: #FFFFFF;
  padding: 0.5rem;
  border-radius: 1rem;
}

.icono-portafolio {
  scale: 0.8;
}

.redes {
  width: 21.82rem;
  display: flex;
  gap: 2rem;
}
.redes img {
  width: 3.96rem;
  height: 3.96rem;
  filter: invert(100%);
}
.redes div {
  display: flex;
  justify-content: left;
}
.redes div:hover p {
  width: 100%;
}
.redes div:hover img {
  scale: 0.9;
}
.redes div p {
  font-size: 1.5rem;
  width: 0;
  overflow: hidden;
  transition: all ease-in-out 0.3s;
  text-align: left;
  font-family: "Lato", sans-serif;
}

@media (max-width: 494px) {
  h3 {
    font-size: 1rem;
  }
  .redes img {
    width: 2.96rem;
    height: 2.96rem;
  }
  .portafolio p {
    font-size: 1.7rem;
  }
}
.header-portafolio {
  margin-top: 1rem;
  height: 5rem;
}
.header-portafolio .centro {
  margin-top: 0.7rem;
  align-items: center;
  display: flex;
}
.header-portafolio .redes {
  width: 21.82rem;
  display: flex;
  gap: 2rem;
}
.header-portafolio .redes img {
  width: 3.96rem;
  height: 3.96rem;
  filter: invert(0);
}
.header-portafolio p {
  color: black;
}

.btn-admin {
  display: flex;
  gap: 1rem;
  width: 50%;
}

.overlayMenu {
  width: 15rem;
  height: 0;
  background-color: #FFFFFF;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.overlayMenu a {
  display: flex;
  height: 33.33%;
}
.overlayMenu a:hover {
  background-color: #f2f2f2;
}
.overlayMenu a p {
  font-size: 2rem;
  font-family: "Lato", sans-serif;
}

.espacio-overlay {
  display: flex;
  width: 45%;
  height: 15rem;
  align-items: flex-start;
  position: absolute;
}

.conetenedor-galerias {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.contenedor-imagenI {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  max-width: 61rem;
  gap: 1rem;
  position: relative;
  justify-content: center;
  align-items: center;
}
.contenedor-imagenI img {
  padding: 0.3rem;
  width: 100%;
  height: auto;
  background-color: white;
}
.contenedor-imagenI:hover .hover-info {
  width: 100%;
  height: 100%;
  border-radius: 0%;
  background: rgb(0, 0, 0);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5046393557) 0%, rgba(0, 0, 0, 0.5) 100%);
}

.galeria-columna {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.hover-info {
  position: absolute;
  background: rgb(0, 0, 0);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%);
  width: 0%;
  height: 0%;
  border-radius: 50%;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.hover-info p {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 2.5rem;
}
.hover-info span {
  margin-left: 1rem;
  font-weight: 400;
  font-size: 2rem;
}

.imagenGrande {
  position: fixed;
  left: 0;
  top: -1rem;
  margin: auto;
  height: 200%;
  width: 100%;
  z-index: 999;
  display: none;
  justify-content: center;
  background: rgb(0, 0, 0);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5046393557) 0%, rgba(0, 0, 0, 0.8) 100%);
}
.imagenGrande .imagenGrende-contenedor img {
  width: auto;
  max-width: 100vw;
  max-height: 100vh;
}

.bodyLock {
  overflow: unset;
}

.login {
  display: flex;
  flex-direction: column;
  width: 40%;
  gap: 2rem;
}

.form-login {
  display: flex;
}

.seccion-contenido {
  margin: auto;
  background-color: #c1d3d3;
  display: flex;
  flex-direction: column;
  width: 60%;
}
.seccion-contenido div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding-left: 2rem;
  padding-right: 2rem;
}
.seccion-contenido div button {
  margin: 0;
  height: 4rem;
  background-color: green;
  cursor: pointer;
}

.creacion {
  width: 100%;
}
.creacion button {
  background-color: green;
}

.lista {
  display: flex;
  flex-direction: column;
  border: 0.3rem solid black;
  width: 80%;
  margin: auto;
  gap: 0.3rem;
}

.segmento {
  background-color: #c1d3d3;
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.segmento img {
  width: 8rem;
  height: auto;
}

.eliminar {
  background-color: red;
  color: white;
  padding: 0.1rem 0.3rem;
}

.btn_actualizar {
  background-color: green;
  padding: 0.1rem 0.3rem;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.formulario {
  width: 60%;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.formulario input, .formulario textarea {
  width: 40%;
}
.formulario label {
  display: flex;
  text-align: left;
  justify-content: left;
}

.filtros {
  display: flex;
  justify-content: center;
  width: 100%;
}
.filtros button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.filtros button:hover {
  scale: 1.1;
}
.filtros p {
  font-family: "Lato", sans-serif;
  margin: 0;
  font-size: 1.6rem;
}

.formulario-filtros {
  height: 11.7rem;
}

.negrita {
  font-weight: bold;
  scale: 1.1;
}